'use client';

import { useState } from 'react';
import Header from '../../components/Header';
import SimulationUI from '../../components/SimulationUI';
import GaiaLink from '../../components/GaiaLink';

export default function SimulationPage() {
  const [tab, setTab] = useState('link');

  return (
    <div className="wrap">
      <Header onTabChange={setTab} />

      {tab === 'link' && (
        <main className="grid">
          <section className="card">
            <GaiaLink />
          </section>
          <aside className="card">
            <h3 style={{ marginTop: 0 }}>Visual Preview — “Future Earth”</h3>
            <div className="muted">
              Live NASA satellite view of Earth and predictive model overlay (coming next)
            </div>
          </aside>
        </main>
      )}

      {tab === 'symbio' && (
        <section className="card">
          <h3 style={{ marginTop: 0 }}>SymbioSphere — Crowd Sensing</h3>
          <p className="muted">This section will allow local observations and uploads next step.</p>
        </section>
      )}

      {tab === 'nexus' && (
        <section className="card">
          <h3 style={{ marginTop: 0 }}>GAIA Nexus — Living Visualization</h3>
          <p className="muted">Filament visualization of Symbiosis results will go here.</p>
        </section>
      )}
    </div>
  );
}
